
.c-bubble-mask {
  width: 100%;
  height: 100%;
  z-index: 20;
  position: fixed;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
}
.c-bubble-top {
  position: fixed;
  top: 20px;
  left: 50%;
  z-index: 30;
  width: 600px;
  padding: 22px 0;
  margin-left: -300px;
  border-radius: 4px;
  font-size: 16px;
  color: #e1e1e1;
  text-align: center;
  font-weight: bolder;
}
.c-bubble-top.z-default {
  background-color: #00a08c;
}
.c-bubble-top.z-warn {
