  background-color: #373737;
}
.c-bubble-dialog .dialog-title.z-center {
  padding-left: 0;
  text-align: center;
}
.c-bubble-dialog .dialog-content {
  padding: 40px 45px;
  background-color: #282828;
  border-bottom: 1px solid #1e1e1e;
  text-align: center;
}
.c-bubble-dialog .dialog-content .dialog-content-title {
  margin-bottom: 30px;
