}
.c-bubble-dialog .dialog-btn {
  height: 80px;
  padding-top: 20px;
  background-color: #373737;
  text-align: center;
}
.c-bubble-dialog .dialog-btn .dialog-btn-cancel {
  margin-right: 60px;
}
.c-bubble-report {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 30;
  width: 350px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.c-bubble-report .report-title {
  width: 100%;
  height: 60px;
  line-height: 60px;
  padding-left: 30px;
  font-size: 16px;
