}
.c-bubble-report .report-content .select-item .select-item-radio {
  display: block;
  float: left;
  width: 30px;
  height: 30px;
  margin-right: 10px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/radio.png');
}
.c-bubble-report .report-content .select-item .select-item-text {
  display: block;
