  height: 30px;
  line-height: 30px;
  font-size: 16px;
  color: #aeaeae;
}
.c-bubble-report .report-content .select-item.z-on .select-item-radio {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/radio_on.png');
}
.c-bubble-report .report-content .select-item:nth-child(3) {
  margin-bottom: 20px;
}
.c-bubble-report .report-content .textare-item {
  width: 290px;
  height: 130px;
