/**
 * 组件样式
 */
.cpm_hide {
  display: none!important;
}
.cpm_left {
  float: left;
}
.cpm_right {
  float: right;
}
.cpm_clear {
  clear: both;
  height: 0;
  width: 100%;
}
.cpm_form_input {
  position: relative;
  border-radius: 4px;
  background-color: #232323;
  border: 1px solid #232323;
}
.cpm_form_input input {
  width: 100%;
  height: 100%;
  color: #aeaeae;
  font-size: 16px;
}
.cpm_form_input span {
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translate(0, -50%);
  color: #505050;
  font-size: 12px;
}
.cpm_form_input.z-error {
  border: 1px solid #e61e5a;
}
.cpm_form_input.z-limit span {
  color: #e61e5a;
}
.cpm_form_textarea {
  position: relative;
  padding: 20px 30px;
  border-radius: 4px;
  background-color: #232323;
  border: 1px solid #232323;
}
.cpm_form_textarea textarea {
  width: 100%;
  height: 100%;
  color: #aeaeae;
  font-size: 16px;
}
.cpm_form_textarea span {
  position: absolute;
  bottom: 20px;
  right: 30px;
  color: #505050;
  font-size: 12px;
}
.cpm_form_textarea.z-error {
  border: 1px solid #e61e5a;
}
.cpm_form_textarea.z-limit span {
  color: #e61e5a;
}
.cpm_tip_number {
  min-width: 20px;
  height: 20px;
  padding: 4px 8px;
  border-radius: 10px;
  font-size: 12px;
  color: #e1e1e1;
  background-color: #00beff;
}
.cpm_button {
  width: 80px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
}
.cpm_button.z-disline {
  display: inline-block;
}
.cpm_button_default {
  width: 80px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
  color: #00beff;
  background-color: #181818;
}
.cpm_button_default.z-disline {
  display: inline-block;
}
.cpm_button_warn {
  width: 80px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
  color: #e61e5a;
  background-color: #181818;
}
.cpm_button_warn.z-disline {
  display: inline-block;
}
.cpm_button_weak {
  width: 80px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
  color: #868686;
