  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
  color: #b4a000;
  background-color: #181818;
  font-weight: bolder;
}
.cpm_button_strong.z-disline {
  display: inline-block;
}
.cpm_button_do {
  width: 80px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 16px;
