  cursor: pointer;
  color: #9664c8;
}
.cpm_button_do.z-disline {
  display: inline-block;
}
.cpm_button_more {
  width: 80px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
  color: #00beff;
  background-color: #181818;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../images/icon/more.png');
  width: 40px;
}
.cpm_button_more.z-disline {
  display: inline-block;
}
.cpm_menu_left {
  position: absolute;
  top: 0;
  left: 0;
  width: 220px;
}
.cpm_menu_left .item {
  width: 100%;
  height: 60px;
}
.cpm_menu_left .item a {
  display: block;
  width: 100%;
  height: 60px;
  line-height: 60px;
  text-align: center;
  font-size: 16px;
  color: #aeaeae;
  background-color: #282828;
  text-decoration: none;
}
.cpm_menu_left .item a:hover {
  color: #e1e1e1;
}
.cpm_menu_left .item a.z-on {
  background-color: #00a08c;
  color: #e1e1e1;
}
.cpm_sub_more {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 10;
  width: 100%;
  overflow-y: hidden;
  text-align: left;
  background-color: #282828;
  color: #868686;
  border: 1px solid #232323;
  border-radius: 4px;
}
.cpm_sub_more .item {
  width: 100%;
  height: 60px;
  line-height: 60px;
  padding-left: 20px;
  cursor: pointer;
}
.cpm_sub_more .item:hover {
  color: #e1e1e1;
  background-color: #181818;
}
.cpm_sub_more.z-center {
  text-align: center;
}
.cpm_sub_more.z-center .item {
  padding-left: 0;
}
.cpm_tag {
  width: 40px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  border-radius: 4px;
  font-size: 14px;
  background-color: #232323;
}
.cpm_tag_man {
  width: 40px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  border-radius: 4px;
  font-size: 14px;
  background-color: #232323;
  border: 1px solid #00a08c;
  color: #00a08c;
}
.cpm_tag_wen {
  width: 40px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  border-radius: 4px;
  font-size: 14px;
  background-color: #232323;
  border: 1px solid #b4a000;
  color: #b4a000;
}
.c-dialog__tip {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  transform: translate(-115%, 0);
}
.c-dialog__tip .tip-arrow {
  position: absolute;
  top: 50%;
  right: -8px;
  margin-top: -8px;
  display: inline-block;
  border: 9px solid;
  border-color: transparent rgba(0, 0, 0, 0.8) rgba(0, 0, 0, 0.8) transparent;
  transform: rotate(-45deg);
}
.c-dialog__tip .tip-text {
  padding: 0 20px;
  height: 60px;
  line-height: 60px;
  background-color: rgba(0, 0, 0, 0.8);
  color: #e61e5a;
  border-radius: 4px;
}
.c-dialog__tip.z-right .tip-arrow {
  right: inherit;
  left: -8px;
  transform: rotate(135deg);
}



