/**
 * user-公共样式
 */
.m-user {
  width: 100%;
  height: 100%;
  min-height: 720px;
  position: relative;
  background-image: url('../../common/images/user/user-bg-01.jpg');
  background-size: cover;
}
.m-user .m-user-wrap {
  height: 100%;
  position: relative;
  margin: 0 auto;
  width: 960px;
}
.m-user-dialog {
  position: absolute;
  right: 0;
  top: 50%;
  width: 450px;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
}
.m-user-dialog__tit {
  width: 100%;
  height: 60px;
  padding-left: 30px;
  line-height: 60px;
  border-bottom: 1px solid #232323;
  color: #868686;
  font-size: 16px;
  background-color: #232323;
  border-radius: 4px 4px 0 0;
}
.m-user-dialog__con {
  width: 450px;
  padding: 50px 60px;
  text-align: center;
  background-color: #2d2d2d;
  border-radius: 0 0 4px 4px;
}
.m-user-dialog__item {
  position: relative;
  width: 100%;
  min-height: 22px;
  margin-bottom: 20px;
}
.m-user-dialog__item:last-child {
  margin-bottom: 0;
}
.m-user-dialog__item.z-60 {
  height: 60px;
}
.m-user-dialog__tip {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  -webkit-transform: translate(-115%, 0);
          transform: translate(-115%, 0);
}
.m-user-dialog__tip .tip-arrow {
  position: absolute;
  top: 50%;
  right: -8px;
  margin-top: -8px;
  display: inline-block;
  border: 9px solid;
  border-color: transparent rgba(0, 0, 0, 0.8) rgba(0, 0, 0, 0.8) transparent;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.m-user-dialog__tip .tip-text {
  padding: 0 20px;
  height: 60px;
  line-height: 60px;
  background-color: rgba(0, 0, 0, 0.8);
  color: #e61e5a;
  border-radius: 4px;
}
.m-user-dialog__input {
  position: relative;
  padding: 20px 30px;
}
.m-user-dialog__input img {
  position: absolute;
  top: 10px;
  right: 5px;
  width: 110px;
  height: 40px;
  cursor: pointer;
}
.m-user-dialog__select {
  float: left;
  color: #868686;
  font-size: 12px;
  cursor: pointer;
}
.m-user-dialog__select .select-icon {
  display: inline-block;
  width: 22px;
  height: 22px;
  margin-right: 10px;
  overflow: hidden;
  background-image: url('../../common/images/user/select-img-01.png');
  cursor: pointer;
  border-radius: 4px;
  vertical-align: middle;
}
.m-user-dialog__select .select-icon.on {
  background-image: url('../../common/images/user/select-img-01_on.png');
  cursor: pointer;
}
.m-user-dialog__btn {
  width: 100%;
  height: 60px;
  line-height: 60px;
}
.m-user-dialog__btn.z-bolder {
  font-weight: bolder;
}
.m-user-dialog__back {
  height: 22px;
  line-height: 22px;
  float: right;
  font-size: 12px;
  color: #868686;
  clear: right;
}
.m-user-dialog__agree {
  font-size: 12px;
  color: #868686;
}
.m-user-dialog__agree a {
  color: #e1e1e1;
}
.m-user-dialog__register {
  font-size: 16px;
  color: #e1e1e1;
  font-weight: bolder;
}
.m-user-dialog__login {
  font-size: 16px;
  color: #e1e1e1;
  font-weight: bolder;
}
.m-user-dialog__input-phone {
  width: 160px;
}
.m-user-dialog__btn-phone {
  width: 160px;
}



